// rem

import eventBus from "./eventBus";

// 
const baseSize = 16
//  rem 
function setRem(delay: boolean) {
  //  1920
  const scale = document.documentElement.clientWidth / 1440
  // Math.min(scale, 2) 2
  const fontSize = baseSize * Math.min(scale, 1.1) + 'px';
  if (!delay) {
    document.documentElement.style.fontSize = fontSize;
  } else {
    setTimeout(() => {
      document.documentElement.style.fontSize = fontSize;
    }, 300);
  }

}
// 
setRem(false);
//  rem
window.onresize = function () {
  setRem(true);
  eventBus.emit('resize');
}

/**
 *  px  px
 * @param num 
 */
export function px(num: number) {
  const scale = Math.min(document.documentElement.clientWidth / 1440, 1.1);
  return num * scale;
}