export class EventBus {
    [x: string]: (type: string, ...args: any[]) => void;
    public events?: any;
    constructor() {
        this.events = this.events || {};
    }
    //event
    //type
    emit(type: string, ...args: Array<any>) {
        let e;
        e = this.events[type];
        // typeevent
        if (Array.isArray(e)) {
            for (let i = 0; i < e.length; i++) {
                e[i].apply(this, args);
            }
        } else {
            e && e[0].apply(this, args);
        }
    };
    //type
    addListener(type: string, fun: Function) {
        const e = this.events[type];

        if (!e) {   //
            this.events[type] = [fun];
        } else {  //
            e.push(fun);
        }
    };

    removeListener(type: string, fun: Function) {
        const i = (this.events[type] ?? []).indexOf(fun);
        if (i > -1) {
            this.events[type].splice(i, 1);
        }
    }
}

const eventBus = new EventBus();
export default eventBus;